<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="80px"
			>
			<el-form-item class="add-item" label="" prop="zhishibiaoti">
				<el-input v-model="ruleForm.zhishibiaoti" 
					placeholder="" clearable :disabled=" false  ||ro.zhishibiaoti"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="zhishifenlei">
				<el-select v-model="ruleForm.zhishifenlei" placeholder="" :disabled=" false  ||ro.zhishifenlei" >
					<el-option
						v-for="(item,index) in zhishifenleiOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.fengmian)" prop="fengmian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
					@change="fengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="fengmian">
				<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.shipin)" prop="shipin">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="2"
					:multiple="true"
					:fileUrls="ruleForm.shipin?ruleForm.shipin:''"
					@change="shipinUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="shipin">
				<div class="viewBtn" v-if="ruleForm.shipin" @click="download(baseUrl + ruleForm.shipin)" type="success"></div>
				<div class="unviewBtn" v-else disabled></div>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="fujian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="3"
					:multiple="true"
					:fileUrls="ruleForm.fujian?ruleForm.fujian:''"
					@change="fujianUploadChange"
					></file-upload>
			</el-form-item>  
			<el-form-item class="add-item" label="" prop="faburiqi">
				<el-date-picker
					:disabled=" false  ||ro.faburiqi"
					format="yyyy  MM  dd "
					value-format="yyyy-MM-dd"
					v-model="ruleForm.faburiqi" 
					type="date"
					placeholder="">
				</el-date-picker> 
			</el-form-item>
			<el-form-item class="add-item" label="" prop="zhishineirong">
				<editor 
					v-model="ruleForm.zhishineirong" 
					class="editor" 
					myQuillEditor="zhishineirong"
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					zhishibiaoti : false,
					zhishifenlei : false,
					fengmian : false,
					shipin : false,
					fujian : false,
					zhishineirong : false,
					faburiqi : false,
					clicktime : false,
					clicknum : false,
					storeupnum : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					zhishibiaoti: '',
					zhishifenlei: '',
					fengmian: '',
					shipin: '',
					fujian: '',
					zhishineirong: '',
					faburiqi: '',
					clicktime: '',
					clicknum: '',
					storeupnum: '',
				},
				zhishifenleiOptions: [],


				rules: {
					zhishibiaoti: [
						{ required: true, message: '', trigger: 'blur' },
					],
					zhishifenlei: [
						{ required: true, message: '', trigger: 'blur' },
					],
					fengmian: [
						{ required: true, message: '', trigger: 'blur' },
					],
					shipin: [
					],
					fujian: [
					],
					zhishineirong: [
					],
					faburiqi: [
					],
					clicktime: [
					],
					clicknum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.faburiqi = this.getCurDate()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='zhishibiaoti'){
							this.ruleForm.zhishibiaoti = obj[o];
							this.ro.zhishibiaoti = true;
							continue;
						}
						if(o=='zhishifenlei'){
							this.ruleForm.zhishifenlei = obj[o];
							this.ro.zhishifenlei = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o]?obj[o].split(",")[0]:'';
							this.ro.fengmian = true;
							continue;
						}
						if(o=='shipin'){
							this.ruleForm.shipin = obj[o];
							this.ro.shipin = true;
							continue;
						}
						if(o=='fujian'){
							this.ruleForm.fujian = obj[o];
							this.ro.fujian = true;
							continue;
						}
						if(o=='zhishineirong'){
							this.ruleForm.zhishineirong = obj[o];
							this.ro.zhishineirong = true;
							continue;
						}
						if(o=='faburiqi'){
							this.ruleForm.faburiqi = obj[o];
							this.ro.faburiqi = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});
				this.$http.get('option/zhishifenlei/zhishifenlei', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.zhishifenleiOptions = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`jiankangzhishi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`jiankangzhishi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			shipinUploadChange(fileUrls) {
				this.ruleForm.shipin = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			fujianUploadChange(fileUrls) {
				this.ruleForm.fujian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

